<template>
  <div class="module-type6">
    <section>
      <div class="open">
        <div
          class="open-class"
          v-for="it in moduleObj.albumList"
          :key="it.programId"
          @click="routerDetailPage(it.programId)"
        >
          <div class="open-class-img">
            <img :src="it.programImg" />
            <img src="../assets/imgs/images/begin5.png" />
          </div>
          <p>{{ it.programTit }}</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "ModuleType6",
  props: {
    moduleObj: Object,
  },
  methods: {
    routerDetailPage(id) {
      this.$router.push({
        path: "/detailMusic",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../assets/fonts/dcloud_iconfont.ttf");
}
.module-type6 {
  margin: 0 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(237, 237, 237);
  section {
    // padding: 15px 0 0px;
    width: 100%;
    .open {
      width: 100%;
      padding: 15px 10px 25px;
      border-radius: 5px;
      box-shadow: 0px 0 5px var(--theme-shadow);
      .open-class {
        width: 100%;
        height: 80px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .open-class-img {
          position: relative;
          width: 70%;
          margin-right: 10px;
          overflow: hidden;
          & img:first-child {
            position: absolute;
            top: 0;
            left: 0px;
            width: 100%;
          }
          & img:last-child {
            position: absolute;
            bottom: 8px;
            right: 15px;
            width: 30px;
          }
        }

        p {
          flex: 1;
          font-family: dcloud_iconfont;
          height: 75px;
          line-height: 1.7em;
          font-size: 13px;
          display: -webkit-box;
          overflow: hidden;
          text-overflow: ellipsis;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
